let loader = require('load-script');

function SiteProxy(config) {
    let objName = config.objName;

    if (!window[objName]) {
        loader(config.script, (err) => err ? console.error('Script error: ', err) : '');
    }

    let objInstance = () => {
        return new Promise((resolve, reject) => {
            let check = () => {
                if (this.instance) {
                    resolve(this.instance);
                } else if (window[objName]) {
                    this.instance = new window[objName](config.apiKey, config.params);
                    resolve(this.instance);
                } else {
                    setTimeout(() => check(), 1000);
                }
            };

            check();
        });
    };

    return new Proxy({}, {
        get: (t, fn) => (...args) => {
            return new Promise((resolve, reject) => {
                objInstance().then(instance => {
                    if (instance[fn]) {
                        instance[fn].apply(instance, args).then((...a) => resolve.apply(this, a)).catch((...e) => reject.apply(this, e));
                    } else {
                        console.error(`${fn} does not exists in ${objName}`);
                    }
                });
            });
        }
    });
}

module.exports = SiteProxy;